footer {
  -webkit-align-items: center;
          align-items: center;
  background-color: #212121;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  display: -webkit-flex;
  display: flex;
  height: 70px;
  -webkit-justify-content: center;
          justify-content: center;
}

footer p {
  color: rgba(255, 255, 255, 0.7);
}
.about {
  background-color: #212121;
  border-radius: 15px;
  padding: 30px 28px;
  margin-bottom: 84px;
}

.intro {
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  display: -webkit-flex;
  display: flex;
  margin-bottom: 30px;
  padding-bottom: 30px;
}

.avatar {
  background-image: url(/me/static/media/avatar.d14730b3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  height: 120px;
  margin-right: 30px;
  width: 120px;
}

.name {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.name p {
  font-size: 24px;
}

.name p + p {
  color: rgba(255, 255, 255, 0.7);
  margin-top: 12px;
}

.contact {
  background-color: #383838;
  border-radius: 15px;
  height: 120px;
  padding: 10px;
  width: 68px;
}

.contact .image {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 48px;
  -webkit-justify-content: center;
          justify-content: center;
  width: 48px;
}

.contact .divider {
  background-color: rgba(255, 255, 255, 0.15);
  display: block;
  height: 1px;
  margin: 4px 0;
}

.description p {
  line-height: 28px;
}

.description p + p {
  margin-top: 16px;
}
.section-title {
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 28px;
  margin-bottom: 9px;
  padding-bottom: 10px;
}

.project-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.card {
  background-color: #212121;
  border-radius: 15px;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(50% - 30px);
          flex-basis: calc(50% - 30px);
  margin: 15px 0;
  padding: 24px 20px;
}

.card.even {
  margin-right: 15px;
}

.card.odd {
  margin-left: 15px;
}

.card-title {
  font-size: 20px;
}

.card-description {
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);;
  border-top: 1px solid rgba(255, 255, 255, 0.15);;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 14px;
  margin-top: 8px;
  opacity: 1;
  padding: 14px 0;
}
* {
  box-sizing: border-box;
}

body {
  background-color: #171717;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body,
p,
ul,
h4,
h5,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  color: #ff5e5e;
  transition: .3s color ease-in-out;
  text-decoration: none;
}

a:hover {
  color: rgba(255, 94, 94, .68);
}

p {
  opacity: .8;
}

main {
  max-width: 580px;
  margin: 80px auto 50px;
}
